.button {
  border: 0;
  border-radius: 14px;
  padding: 15px 32px;
  width: auto;
  font-size: 18px;
  color: white;
  border: 1px solid white;
  cursor: pointer;
  font-weight: 600;
  transition: transform ease-in-out 0.1s;
  &:active {
    transition: transform ease-in-out 0.1s;
    transform: translateY(1px);
  }
}
